<template>
  <div>
    <v-card style="max-width:98%; margin-top: 10px;">
      <v-card-title>
        用户管理
        <v-spacer/>
        <v-text-field
          v-model="search"
          append-icon="mdi-magnify"
          label="搜索"
          hide-details
        />

        <v-dialog v-model="addUserDialog"  max-width="500px" >
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              class="mx-12"
              depressed
              dark
              v-bind="attrs"
              v-on="on"
              color="blue"
            >新增用户
            </v-btn>
          </template>
          <v-card>
            <v-card-title>
              添加新用户
            </v-card-title>
            <v-card-text>
              <ValidationObserver
                ref="addUser"
                v-slot="{ validated }"
              >
                <VTextFieldWithValidation
                  :validated="validated"
                  v-model="addUserInfo.username"
                  rules="required"
                  label="用户名"
                />
                <VTextFieldWithValidation
                  :validated="validated"
                  v-model="addUserInfo.email"
                  rules="required"
                  label="邮箱"
                />
                <VTextFieldWithValidation
                  :validated="validated"
                  v-model="addUserInfo.password"
                  rules="required"
                  label="密码"
                />
              </ValidationObserver>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="addUserDialogClose">{{ $t('appBar.dialogClose') }}</v-btn>
              <v-btn color="blue darken-1" text @click="addUser">{{ $t('appBar.dialogAdd') }}</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-card-title>
      <v-data-table
        :headers="headers"
        :items="userInfo"
        :search="search"
        :items-per-page="userInfo.length"
        hide-default-footer
      >
        <template v-slot:[`item.actions`]="{ item }">
          <v-icon
            small
            class="mr-2"
            title="编辑"
            @click="setUserInfo(item.id)"
          >
            mdi-pencil
          </v-icon>
          <v-icon
            small
            class="mr-2"
            :title="item.isEnabled? '注销账户' : '恢复账号'"
            @click="logoffOrRestore(item, item.isEnabled)"
          >
            {{ item.isEnabled? 'mdi-account-remove' : 'mdi-restore' }}
          </v-icon>
          <v-btn
            dark
            small
            depressed
            color="blue"
            @click="setUserPassw(item.id)"
          >
            重置密码
          </v-btn>
        </template>
      </v-data-table>
    </v-card>

    <v-dialog v-model="setUserPasswDialog" max-width="400px">
      <v-card>
        <v-card-title>
          设置新密码
        </v-card-title>
        <v-card-text>
          <ValidationObserver
            ref="setUserPassw"
            v-slot="{ validated }"
          >
            <VTextFieldWithValidation
              :validated="validated"
              v-model="setUserPasswParam.userPassword"
              rules="required"
              label="新密码"
            />
          </ValidationObserver>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="setUserPwDialogClose">{{ $t('appBar.dialogClose') }}</v-btn>
          <v-btn color="blue darken-1" text @click="setUserPasswConf">{{ $t('appBar.dialogAdd') }}</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="setUserInfoDialog" max-width="400px">
      <v-card>
        <v-card-title>
          编辑信息
        </v-card-title>
        <v-card-text>
          <ValidationObserver
            ref="setUserInfo"
            v-slot="{ validated }"
          >
            <VTextFieldWithValidation
              :validated="validated"
              v-model="setUserInfoParam.nickname"
              rules="required"
              label="昵称"
            />
          </ValidationObserver>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="setUserInfoDialogClose">{{ $t('appBar.dialogClose') }}</v-btn>
          <v-btn color="blue darken-1" text @click="setUserInfoConf">{{ $t('appBar.dialogAdd') }}</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { ValidationObserver } from "vee-validate";
import VTextFieldWithValidation from "@/views/components/VTextFieldWithValidation";
export default {

  components: {
    ValidationObserver,
    VTextFieldWithValidation,
  },

  data:() => ({
    search: '',
    setUserInfoDialog: false,
    setUserPasswDialog: false,
    addUserDialog: false,
    headers: [
      {
        text: '用户ID',
        align: 'start',
        sortable: false,
        value: 'id',
      },
      { 
        text: '用户名',
        value: 'username'
      },
      {
        text: '是否可用',
        value: 'isEnabled',
        sortable: false
      },
      { 
        text: '操作',
        value: 'actions',
        sortable: false,
      }
    ],
    addUserInfo: {
      code: '1234',
      email: '',
      username: '',
      password: ''
    },
    setUserInfoParam: {
      id: 0,
      nickname: ''
    },
    logoffOrRestoreParam: {
      userId: 0,
      index: 0
    },
    setUserPasswParam: {
      id: 0,
      userPassword: ''
    }
  }),

  computed: {
    ...mapGetters(['userInfo']),
  },

  created() {
    this.$store.dispatch('getAllUsers')
  },

  methods: {
    addUserDialogClose(){
      this.addUserInfo.username = ''
      this.addUserInfo.email = ''
      this.addUserInfo.password = ''
      this.$refs.addUser.reset()
      this.addUserDialog = false
    },
    addUser() {
      this.$refs.addUser.validate().then( isValidate => {
        if(isValidate) {
          this.$store.dispatch('addUser', JSON.parse(JSON.stringify(this.addUserInfo)))
          this.addUserInfo.username = ''
          this.addUserInfo.email = ''
          this.addUserInfo.password = ''
          this.addUserDialog = false
        }
      })
    },
    setUserInfo(userId) {
      this.setUserInfoParam.id = userId
      this.setUserInfoDialog = true
    },
    setUserInfoDialogClose() {
      this.setUserInfoParam.nickname = ''
      this.setUserInfoDialog = false
    },
    setUserInfoConf() {
      this.$refs.setUserInfo.validate().then(isValidate => {
        if(isValidate){
          this.$store.dispatch('setUserInfo', this.setUserInfoParam).then(() => {
            this.setUserInfoParam.nickname = ''
            this.setUserInfoDialog = false
          })
        }
      })
    },
    logoffOrRestore(item, isEnabled) {
      this.logoffOrRestoreParam.userId = item.id
      this.logoffOrRestoreParam.index = this.userInfo.indexOf(item)
      if(isEnabled) { //logoff
        confirm('是否将该用户注销？') && this.$store.dispatch('logoffUser', this.logoffOrRestoreParam)
      }else{  //restore
        this.$store.dispatch('restoreUser', this.logoffOrRestoreParam)
      }
    },
    setUserPwDialogClose() {
      this.$refs.setUserPassw.reset()
      this.setUserPasswParam.userPassword = ''
      this.setUserPasswDialog = false
    },
    setUserPassw(userId) {
      this.setUserPasswParam.id = userId
      this.setUserPasswDialog = true
    },
    setUserPasswConf() {
      this.$refs.setUserPassw.validate().then(isValidate => {
        if(isValidate){
          this.$store.dispatch('setUserPassw', this.setUserPasswParam).then(() => {
            this.setUserPasswParam.userPassword = ''
            alert('修改成功')
          })
          this.setUserPasswDialog = false
        }
      })
    }
  }
}
</script>